/* ==============================================================
   Alphabetic Paginator
   style declarations for alphabeticPaginator.xhtml
   ==============================================================*/
/* ALPHABETIC PAGINATOR */
/* BLOCK */
.alphabetic-paginator {
    /* ELEMENT */
    ul {
    }
    li {
        border: @border;
        &.active {
            a {
                color: white;
                background-color: @ci-color1;
                text-decoration: none;
            }
        }
    }
    a {
        background-color: @bg-content-box;
        color: @lk-content-box;
        &:hover,
        &:active,
        &:focus {
            background-color: @bg-hover;
            color: @fg-general;
        }
    }
}

