    &__text {
    }
    &__feed {
    }
    &__item {
        background-color: @bg-content-box;
        color: @fg-content-box;
        border: @border;
    }
    a {
        color: @lk-content-box;
        &:focus,
        &:hover,
        &:active {
            color: @lh-content-box;
        }
    }
    &__item-title {
        h2 {
        }
        a {
        }
    }
    &__item-image {
        img {
        }
    }
    &__item-date {
    }
    &__item-metadata {
        dt {
        }
        dd {
        }
    }
}

