/* ==============================================================
   View Fulltext
   style declarations for viewFulltext.xhtml
   ==============================================================*/
/* VIEW FULLTEXT */
/* BLOCK */
.view-fulltext {
    /* ELEMENT */
    &__wrapper {
        padding: 10px;
        border: @border;
        border-top: 0;
        background-color: @bg-content-box;
    }
    &__content {
        color: @fg-general;
        & when (@colorModes = true) {
            .mx-light-mode-rules ({
				color: @fg-general;
			});;
            .mx-dark-mode-rules ({
				color: @bg-content-box;
			});;
        }
        a {
            & when (@colorModes = true) {
                .mx-light-mode-rules ({
					color: @fg-general;
					text-decoration: underline;
					font-weight: 500;
				});;
                .mx-dark-mode-rules ({
					color: @bg-content-box;
					text-decoration: underline;
					font-weight: 500;
				});;
            }
        }
    }
    &__box {
        pre {
        }
    }
}

