/* BREADCRUMBS */
/* BLOCK */
.breadcrumbs {
    &__wrapper {
        font-size: @fontSmall;
    }
    &__list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;

        li {
            display: flex;
            align-items: center;
        }
    }
    /* ELEMENT */
    ul {
    }
    li {
    }
    a,
    span {
    }
    a {
    }
    span {
    }
    &__starttext {
        a,
        span {
        }
    }
    &__starttext + li {
        a,
        span {
        }
    }
    &__crumb + li:before {
    }
}

