/* ==============================================================
   User Bookmarks
   style declarations for userBackendBookmark.xhtml
   ==============================================================*/
/* USER BOOKMARKS */
/* BLOCK */
.user-bookmark {
    /* ELEMENT */
    h1 {
    }
    /* ADD */
    &__add-bookmark {
        input {
            color: @fg-general;
        }
        button,
        a {
            color: @fg-general;
            svg {
                stroke: @fg-general;
            }
        }

        ::placeholder {
            color: @fg-general;
            opacity: 0.5;
        }
    }
    /* PANEL */
    &__panel {
        background-color: @bg-content-box;
        border: @border;
    }
    &__panel-header {
        h2,
        h3 {
        }
        a {
        }
        small {
        }
    }
    &__panel-body {
        p {
        }
    }
    &__panel-action {
        a,
        button {
            color: @fg-general;
        }
    }
}

