/* ==============================================================
   Thumbnail paginator
   Style declarations for thumbnailPaginator.xhtml
   ==============================================================*/

/* THUMBNAIL PAGINATOR */

.sidebar-scroll-preview {
    &__inner {
    }
    &__grid {
        .simplebar-track {
        }
        .simplebar-scrollbar {
            &:before {
                & when (@colorModes = true) {
                    .mx-light-mode-rules ({
						background-color: #000;
					});;
                    .mx-dark-mode-rules ({
						background-color: #fff;
					});;
                }
            }
        }
    }
    &__item {
    }
    &__button,
    &__link {
        color: @fg-general;
        &:hover,
        &:active,
        &:focus {
            color: @fg-general;
        }
        &.-highlighted {
            background-color: @ci-color1;
            color: white;
            outline: none;
        }
    }
    &__button-text {
    }
    &__image {
        &.-loaded {
        }
    }
}

@{RTL} {
    .sidebar-scroll-preview {
        &__grid {
            .simplebar-track {
            }
        }
    }
}

