}

/* BREAKPOINT FOR TABLETS */
@media screen and (max-width: @smallBreakpoint) {
    .thumbnail-paginator {
        &__item {
            &.-activePage {
                .thumbnail-paginator__image {
                }
            }
        }
        &__image {
            height: 160px;
        }
    }
}

