/* ==============================================================
   Style declarations for userBackendAnnotations.xhtml
   ==============================================================*/
#userAnnotations {
    .admin__search-action {
        background: @bg-content-box;
        border: 1px solid #ccc;
        & when (@colorModes = true) {
            .mx-dark-mode-rules ({
				border: none;
			});;
            .mx-light-mode-rules ({
				border: 1px solid #ccc;
			});;
        }
        &:hover,
        &:active,
        &:focus {
            .btn--icon {
                border: @fg-general;
                & when (@colorModes = true) {
                    .mx-dark-mode-rules ({
						color: @fg-general;
					});;
                    .mx-light-mode-rules ({
						border: @fg-general;
					});;
                }
            }
        }
    }
    .data-table-paginator__button {
        &:hover,
        &:active,
        &:focus {
            color: @fg-general;
        }
    }
}

