/* ==============================================================
   Style declarations for userBackendComments.xhtml
   ==============================================================*/
/* USER COMMENTS TABLE */
#userCommentsTable {
}

.user-comments {
    .admin__table-sorting {
        &__heading {
            color: @fg-general;
        }
    }

    &__table {
    }
    &__table-title {
    }
    &__table-body {
    }

    .admin__table-sorting-arrow-hidden {
    }
    &__table-title-target,
    &__table-title-comment,
    &__table-title-date {
        &:hover {
            .admin__table-sorting-arrow-hidden {
            }
        }
    }
    &__table-entry {
        & when (@colorModes = true) {
            .mx-dark-mode-rules ({
				&:nth-child(2n+1) {
						background: @bg-content-box;
						&:hover {
							background-color: @bg-hover;
						}
				}
			});;
            .mx-light-mode-rules ({
		
			});;
        }
        &:hover {
            .user-comments__table-entry-action-link {
            }
        }
    }
    &__table-entry-label {
        color: @fg-general;
    }
    &__table-entry-label-record {
    }
    &__table-entry-action-link {
    }
    .admin__search-action {
        background: @bg-content-box;
        border: 1px solid #ccc;
        & when (@colorModes = true) {
            .mx-dark-mode-rules ({
			border: none;
		});;
            .mx-light-mode-rules ({
			border: 1px solid #ccc;
		});;
        }
        &:hover,
        &:active,
        &:focus {
            .btn--icon {
                border: @fg-general;
                & when (@colorModes = true) {
                    .mx-dark-mode-rules ({
			color: @fg-general;
		});;
                    .mx-light-mode-rules ({
			border: @fg-general;
		});;
                }
            }
        }
    }
    .data-table-paginator__button {
        &:hover,
        &:active,
        &:focus {
            color: @fg-general;
        }
    }
    &__edit-comment-button {
        color: @lk-general;
        &:hover {
            color: @lk-general;
        }
    }
}

