/* MY SEARCHES */
/* BLOCK */
.my-searches {
    background: @bg-content-box;
    border: @border;
    /* ELEMENT */
    h1 {
    }
    &__table {
        margin-bottom: 0;
        color: @fg-general;
        thead {
            tr {
                th {
                    &:nth-child(1) {
                    }
                    &:nth-child(2) {
                    }
                    &:nth-child(3) {
                    }
                }
            }
        }
        tbody {
            tr {
                &:hover {
                    background: @bg-hover;
                }
                td {
                    &:nth-child(1) {
                    }
                    &:nth-child(2) {
                    }
                    &:nth-child(3) {
                    }
                }
            }
        }
    }
    &__action,
    &__delete-button {
        color: @lk-general;
        &:hover,
        &:active,
        &:focus {
            color: @lk-general;
        }
    }
    &__action {
    }
    &__delete-button {
        color: @red;
        &:hover,
        &:active,
        &:focus {
            color: @red;
        }
    }
    &__delete-modal {
    }
}

