/* ==============================================================
   Widget Copyright Information
   style declarations for widget_copyrightInfo.xhtml
   ==============================================================*/
.widget-copyright {
    border-top: @border;
    background: white;
    .rs-svg-path {
        fill: #000;
    }
    .paragraph-svg-path {
        fill: #000;
    }
    & when (@colorModes = true) {
        .mx-light-mode-rules ({
			.rs-svg-path {
				fill: #000;
			}
			.paragraph-svg-path {
				fill: #000;
			}
		});;
        .mx-dark-mode-rules ({
			.rs-svg-path {
				fill: #fff;
			}
			.paragraph-svg-path {
				fill: #fff;
			}
		});;
    }
    & when (@colorModes = true) {
        .mx-light-mode-rules ({
			background: white;
		});;
        .mx-dark-mode-rules ({
			background: @bg-content-box;
		});;
    }
    &__dot {
        color: #000;
        & when (@colorModes = true) {
            .mx-light-mode-rules ({
				color: #000;
			});;
            .mx-dark-mode-rules ({
				color: white;
			});;
        }
    }
    &__status-icon {
        color: @black;

        & when (@colorModes = true) {
            .mx-light-mode-rules ({
				color: @black;
			});;
            .mx-dark-mode-rules ({
				color: @white;
			});;
        }
    }
}

