/* ==============================================================
   Widget Search Facets
   style declarations for widget_searchFacets.xhtml
   ==============================================================*/
/* WIDGET SEARCH FACETS */
.widget-search-facets {
    &__body {
        ul {
        }

        li {
            &.active {
                a {
                    background-color: @bg-hover;
                }
            }
        }
    }

    &__list {
        scrollbar-color: @lk-content-box transparent;
    }

    /* FILTER */
    &__filter {
        top: 0;
        input,
        button {
        }
        input {
            background-color: @ci-hovercolor1;
            &::-webkit-input-placeholder {
            }
            &:-moz-placeholder {
            }
            &::-moz-placeholder {
            }
            &:-ms-input-placeholder {
            }
            &.in {
            }
        }
        [data-toggle='filter-input'] {
        }
    }

    &__filter-input {
    }

    /* COLLECTION/GROUPS */
    &__collection,
    &__groups {
        ul {
        }
    }

    &__collection {
    }

    &__groups {
    }

    /* COLLAPSE FACETS */
    &__collapse {
        a {
        }
    }

    &__boolean {
    }

    &__boolean-button {
        color: @fg-general;
        border: @border;

        & when (@colorModes = true) {
            .mx-dark-mode-rules ({
				background: @bg-active;
			});;
            .mx-light-mode-rules ({
				background: transparent;
			});;
        }
        &:hover {
            & when (@colorModes = true) {
                .mx-dark-mode-rules ({
					color: @fg-general;
					background: @bg-hover;
				});;
                .mx-light-mode-rules ({
					color: @fg-general;
					background: @bg-hover;
				});;
            }
        }

        &.-active {
            & when (@colorModes = true) {
                .mx-dark-mode-rules ({
					// color: white;
					background-color: @bg-hover;
				});;
                .mx-light-mode-rules ({
					background-color: @bg-hover;
				});;
            }
            &:hover {
                .mx-dark-mode-rules ({
					color: @fg-general;
					background-color: @bg-hover;
				});;
                .mx-light-mode-rules ({
					color: @fg-general;
					background-color: @bg-hover;
				});;
            }
        }
    }
}

