/* ==============================================================
   View Fullscreen Metadata 
   ==============================================================*/
/* FULLSCREEN METADATA */
.fullscreen {
    .metadata {
        &__title-wrapper {
        }
        &__title {
        }
        /* ELEMENTS / EVENTS */
        &__elements,
        &__events {
            dd {
            }
        }
        &_wrapper {
        }
        &__element,
        &__event {
        }
        &__element-dl,
        &__event-dl {
            dt {
            }
            dd {
            }
        }
        &__element-value,
        &__event-value {
            &:last-child {
            }
        }
        /* ELEMENT */
        &__element {
            &-level-0 {
                .metadata__element-dl {
                }
                &:hover {
                }
                &:first-child {
                }
            }
            &-level-1 {
                .metadata__element-dl {
                }
                .metadata__element-dl {
                }
                dt {
                    &::before {
                    }
                }
            }
            &-level-2 {
                .metadata__element-dl {
                }
            }
            &-level-3 {
            }
            &-level-4 {
            }
        }
        &__elements {
            &.level-0 {
            }
            &.level-1 {
            }
            &.level-2 {
            }
        }
        &__elements {
            &-level-0 {
            }
            &-level-1 {
            }
            &-level-2 {
            }
        }
        &__elements-h2 {
            &:first-child {
            }
        }
        &__element-wrapper-single {
            dt {
            }
        }
        &__element {
        }
        &__element-dl {
            dt {
            }
            dt {
            }
            dd {
            }
        }
        &__element-value {
        }
        /* EVENT */
        &__events {
        }
        &__event {
        }
        &__event-dl {
            dt {
            }
            dd {
            }
        }
        &__event-value {
        }
        &__grouped-wrapper {
            border: none;
            .metadata__element-dl {
                &:last-child {
                }
            }
            dt,
            dd {
            }
            .metadata__element-wrapper {
            }
        }
        &__ungrouped-wrapper {
            border: none;
        }
        &__element-ungrouped {
            dt {
            }
        }
        &__wrapper {
            // background-color: @bg-content-box;
            &.-isBoxed {
                border: none;
                .metadata__ungrouped-wrapper {
                }
                .metadata__element-wrapper-single {
                }
            }
            a {
                color: @lk-content-box;
            }
        }
        &__heading {
            &.level-1,
            &.level-2 {
                &:before {
                }
            }
            &.level-1 {
            }
            &.level-2 {
            }
        }
    }

    /* DOWNLOADS */
    #viewMetadataDownloads {
        ul {
            li {
                a {
                }
                .btn {
                }
                /* BLOCK */
                &.dl-links {
                    /* ELEMENT */
                    &__mets {
                    }
                    &__mets-all {
                    }
                    &__marcxml {
                    }
                    &__dc {
                    }
                    &__ese {
                    }
                    &__dfg {
                    }
                    &__opac {
                    }
                    &__pdf {
                    }
                    &__epub {
                    }
                }
            }
        }
    }
}

