/* SOLR QUERY */
/* BLOCK */
.tpl-solr-query {
    /* ELEMENT */
    &__content {
        margin-left: 20px;
        margin-top: 30px;
    }
    /* SOLR-QUERY */
    .solr-query-collection {
        &__thumbs {
            padding: 10px;
            margin-bottom: 20px;
            border: @border;
            background-color: @bg-content-box;
        }
        &__thumb,
        &__image,
        &__title {
            background-color: @bg-content-box;
        }
        &__thumb {
            border: @border;
        }
    }
}

