/* FAQ */
/* BLOCK */
.tpl-faq {
    /* ELEMENT */
    h1 {
    }
    &__description,
    &__list {
        background-color: @bg-content-box;
        border: @border;
    }
    &__description {
    }
    &__list {
        .card {
            background-color: @bg-general;
            h2 a,
            a::before {
                color: @fg-general;
                &:focus,
                &:hover,
                &:active {
                    color: @fg-general;
                }
            }
        }
        .card-heading {
            &:hover {
            }
        }
        .card-title {
            a {
                &:hover,
                &:active,
                &:focus {
                    color: @fg-general;
                }
            }
        }
        .card-body {
            background-color: @bg-content-box;
            .tpl-faq-single {
                &__content {
                    background-color: @bg-content-box;
                    padding: 0;
                    border: none;
                }
            }
        }
    }
}

