/* COLLECTION TILED */
/* BLOCK */
.tpl-collection-tiled {
    /* ELEMENT */
    &__text,
    &__wrapper {
        border: none;
    }
    &__text {
        margin-bottom: 0;
        padding-bottom: 2rem;
        border: none;
    }
    &__wrapper {
        border-top: 0;
    }
    .collection-tiled__tile {
        background-color: @bg-content-box;
        border: @border;
    }
    .collection-tiled__header {
        border-bottom: @border;
    }
}

