// WIDGET STYLE
.widget-search-advanced-help {
    &__highlight {
        background: #f1f1f1;
        color: #6d0000;
        border-radius: 4px;
    }

    &__monospace {
        font-family: monospace, sans-serif;
        text-transform: uppercase;
    }
}

