/* ==============================================================
   Navigation
   style declarations for navigation.xhtml
   ==============================================================*/
/* NAVIGATION */
/* BLOCK */
.navigation {
    background-color: @ci-color1;
    li {
        &.active > a,
        &.active > button {
            background-color: @ci-hovercolor1;
        }
    }
    .btn {
        &:active:focus,
        &.active,
        &.active.focus,
        &.active:focus,
        &.focus,
        &:active.focus {
            color: @white;
        }
    }
    a,
    button {
        padding: 0.6rem 1.2rem;
        font-size: @fontNormal;
        color: @white;
        &:hover,
        &:focus,
        &:active {
            color: @white;
            background-color: @ci-hovercolor1;
        }
    }
    .caret {
        color: @white;
    }
    /* STANDARD MENU */
    &__submenu-trigger {
        button {
            transition: none;
        }
    }
    &__submenu {
        border: none;
        top: calc(~'100%');
        border-color: transparent;
        background-color: transparent;
        li {
            background-color: @ci-hovercolor1;
            &:last-child {
                border-bottom: 0;
            }
            &.divider {
            }
            &.active {
                &:hover {
                    a,
                    button {
                        &:hover,
                        &:focus,
                        &:active {
                            background-color: @ci-color1;
                        }
                    }
                }
            }
        }
        a,
        button {
            font-size: @fontMedium;
            &:hover,
            &:focus,
            &:active {
                background-color: @ci-color1;
            }
        }
        .caret {
            transform: rotate(-90deg);
        }
        &.in {
        }
        &.level-2,
        &.level-3,
        &.level-4,
        &.level-5 {
            top: -1px;
        }
    }
    /* MEGA MENU */
    &__megamenu-trigger {
    }
    &__megamenu-wrapper {
        &.in {
        }
    }
    &__megamenu-close {
    }
    &__megamenu-element {
        &.level-1 {
        }
        &.level-2 {
        }
    }
    &__megamenu-heading {
    }
    &__megamenu-item {
    }
}

// MOBILE ADAPTIONS

#pageNavigationMobile {
    .navigation {
        &__submenu-trigger {
            > button > .caret {
                border: none;
                width: 20px;
                height: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                &:before {
                    content: '';
                    width: 18px;
                    height: 18px;
                    background-color: currentColor;
                    .mx-icon-mask(plus);
                }
            }

            &.active {
                > button > .caret {
                    &:before {
                        .mx-icon-mask(minus);
                    }
                }
            }
        }

        &__submenu {
            &.in {
                display: none;
            }
            li {
                background-color: @ci-color1;
            }
        }
    }
}

@{RTL} {
    .navigation {
        &__submenu {
            .caret {
                transform: rotate(90deg);
            }
        }
    }
}

