}

/* BREAKPOINT FOR TABLETS */
@media screen and (max-width: @smallBreakpoint) {
    .navigation {
        position: relative;
        width: 100%;
        overflow-y: auto;
        transition: top 0.3s ease;
        z-index: 2031;
        background: unset;
        &.in {
            top: 0;
        }
        form > ul {
            border-top: 1px solid @white;
            border-bottom: 1px solid @white;
        }
        ul {
            float: none;
        }
        li {
            display: list-item;
            float: none;
            border-bottom: 1px solid @white;
            &:last-child {
                border-bottom: 0;
            }
        }
        a,
        button {
            width: 100%;
            text-align: left;
        }
        &__submenu {
            position: relative;
            float: none;
            top: inherit;
            left: inherit;
            min-width: inherit;
            padding-inline-start: 2rem;
            border: 0;
            z-index: 2030;
            .caret {
                transform: unset;
            }
            &.level-2,
            &.level-3,
            &.level-4,
            &.level-5 {
                left: inherit;
                width: 100% !important;
                &.left {
                    left: inherit;
                }
            }
        }
    }
    @{RTL} {
        .navigation {
            &__submenu {
                .caret {
                    transform: unset;
                }
            }
        }
    }
}

