/* SIMPLE */
/* BLOCK */
.tpl-simple {
    /* ELEMENT */
    &__text,
    &__media {
        margin-bottom: 0;
        padding: 10px;
    }
    &__text {
        border-bottom: 0;
        &:last-child {
            border-top: 0;
        }
    }
    &__media {
        border-top: 0;
        border-bottom: 0;
        figcaption {
        }
    }
}

