/* BUTTONS */

/* MODIFICATIONS */
.btn--default {
    color: @btnDefault;
    &:hover,
    &:active,
    &:focus,
    &:active:focus,
    &.active,
    &.active.focus,
    &.active:focus,
    &.focus,
    &:active.focus {
        color: @black;
    }
}

.btn--success {
    color: @btnSuccess;
    &:hover,
    &:active,
    &:focus {
    }
}

.btn--danger {
    color: @btnDanger;
    &:hover,
    &:active,
    &:focus {
    }
}

.btn--full {
    color: @white;
    background-color: @ci-color1;
    border-color: @ci-color1;
    &:hover,
    &:active,
    &:focus,
    &.active:focus,
    &.focus,
    &:active.focus {
        color: @white;
        background-color: @ci-hovercolor1;
        border-color: @ci-hovercolor1;
    }
}

.btn--clean {
}

.btn--link {
    color: @lk-general;
    &:hover,
    &:active,
    &:focus {
        color: @lh-general;
        & when (@colorModes = true) {
            .mx-light-mode-rules ({
				color: @middleblack;
			});;
            .mx-dark-mode-rules ({
				color: @lh-general;
			});;
        }
    }
}

.btn--hidden {
}

.btn--inactive {
}

.btn--icon {
    &:hover,
    &:active,
    &:focus {
    }
}

a.btn--icon {
}

