/* ==============================================================
   View Fullscreen Image Controls
   ==============================================================*/
/* FULLSCREEN IMAGE CONTROLS */
.fullscreen {
    .image-controls {
        &.faded {
        }
        /* DOCUMENT OPTIONS */
        &__options {
        } 
        /* ACTIONS */
        &__actions {
            ul {
            }
            li { 
            }
        }
        &__action {
			&.next,
			&.prev {
				background: @bg-content-box;
			}
            /* ELEMENT */
            a,
            span,
            button {
                &:hover,
                &:active,
                &:focus {
                }
            }
            span {
            }
            [class*='image-'] {
            }
            &.inactive {
                a,
                span,
                button {
                }
            }
            /* ELEMENT */
            &.start {
                a,
                span,
                button {
                }
            }
            &.page-browse,
            &.select {
            }
            &.page-browse {
                a,
                span {
                }
            }
            &.prev {
                a,
                span,
                button {
                }
            }
            &.select-text {
                span {
                }
            }
            &.select {
                .form-control__select {
                    select {
                    }
                }
                .custom-control--select {
                    select {
                    }
                    &:after {
                    }
                }
            }
            &.next {
                a,
                span,
                button {
                }
            }
            &.end {
                a,
                span,
                button {
                }
            }
            &.double-page-view,
            &.single-page-view,
            &.switch-pages {
                [class*='image-'] {
                }
            }
            &.double-page-view {
                a,
                span,
                button {
                }
            }
            &.single-page-view {
                a,
                span,
                button {
                }
            }
            &.switch-pages {
                a,
                span,
                button {
                }
                svg {
                }
            }
            &.rotate-left,
            &.rotate-right {
                [class*='image-'] {
                }
            }
            &.rotate-left {
                a,
                span,
                button {
                }
            }
            &.rotate-right {
                a,
                span,
                button {
                }
            }
            &.reset {
                a,
                span,
                button {
                }
            }
            &.fullscreen {
            }
            &.zoom-slider-wrapper {
                .zoom-slider {
                }
                .zoom-slider-handle {
                }
            }
        }
    }
}

