/* ==============================================================
   User Backend Tables
   style declarations for user backend tables
   ==============================================================*/
.user-backend-table {
    border-top: @border;
    border-bottom: @border;
    thead {
        tr {
            th {
                border-right: 1px solid @middlegray;
                border-left: 1px solid @middlegray;
            }
        }
    }
    tbody {
        tr {
            td {
                border-right: 1px solid @middlegray;
                border-left: 1px solid @middlegray;
                a {
                }
                .btn {
                    margin-bottom: 0;
                }
            }
        }
    }
}

