/* ==============================================================
   Browse
   style declarations for browse.xhtml
   ==============================================================*/
/* BROWSE */
/* BLOCK */
.collections {
    a,
    button {
        color: @lk-content-box;
        &:focus,
        &:hover,
        &:active {
            color: @lk-content-box;
        }
    }

    border-top: none;
    /* ELEMENT */
    &__wrapper {
        background-color: @bg-content-box;
    }
}
.collection {
    border: @border;
    border-top: 0;
    /* ELEMENT */
    .row {
    }
    &__item {
        border-top: @border;
    }
    &__structure,
    &__title,
    &__description-toggle,
    &__rss {
        a,
        button {
        }
    }
    &__structure {
    }
    &__title {
    }
    &__top-element {
    }
    &__description-toggle,
    &__rss {
    }
    &__description {
    }
    /* MODIFICATION */
    &__item--level-0 {
    }
}

