/* ==============================================================
   Input field paginator
   style declarations for component inputFieldPaginator.xhtml
   ==============================================================*/
/* BLOCK */

.inputfield-paginator {
    ul {
    }
    &__navigate {
        border: @border;
        background-color: @bg-content-box;
        &:hover {
            background: @bg-hover;
        }
        &.inactive {
            &:hover {
                background-color: @bg-content-box;
            }
        }
        svg {
            fill: @black;
            & when (@colorModes = true) {
                .mx-dark-mode-rules ({
					fill: @white;
				});;
                .mx-light-mode-rules ({
					fill: @black;
	  			});;
            }
        }
    }
    &__navigate-text {
    }
    &__page-number-label {
    }
    &__page-number-input {
        input {
            border: none;
            outline: none;
            background-color: transparent;
            color: @fg-general;
            & when (@colorModes = true) {
                .mx-dark-mode-rules ({
					color: @fg-general;
				});;
                .mx-light-mode-rules ({
					color: @fg-general;
	  			});;
            }
        }
    }
}

