/* VIEW THUMBS */
/* BLOCK */
.view-thumbs {
    /* ELEMENT */
    &__thumbnails {
        margin: 0;
    }
    &__thumbnail {
        border: @border;
        background-color: @bg-content-box;
        &:hover {
            background-color: @bg-hover;
        }
    }
    &__thumbnail-image {
        a {
        }
        img {
        }
        figcaption {
        }
    }
    &__thumbnail-image-order {
    }
    &__thumbnail-image-fulltext {
    }
}

