/* ==============================================================
	Styling for content component search facets
   ==============================================================*/

.content-component__search-facets {
    &-wrapper {
    }
    &-list {
    }
    &-list-entry {
    }
    &-link {
        border: @border;
        background-color: @bg-content-box;
        color: @lk-content-box;
        &:hover,
        &:active,
        &:focus {
            background-color: @bg-hover;
            color: @fg-general;
        }
        &.-active {
            color: @fg-content-box;
            background-color: @bg-content-box;
            font-weight: 700;
        }
    }
    &-reset {
    }
}

