/* ==============================================================
   View Fullscreen Annotiations 
   ==============================================================*/
/* FULLSCREEN ANNOTATIONS */
.fullscreen {
    #widgetAnnotations {
        margin-top: 0;
    }
    .widget-annotations {
        .widget__title {
            display: none !important;
            visbility: hidden !important;
        }
        .widget__body {
            display: block;
            border: none;
            padding: 0;
        }
    }
}

