/* TAGS */
#ta {
    h1 {
        & + h2 {
            margin-bottom: 0;
            border: @border;
            border-bottom: 0;
            background-color: @bg-content-box;
            color: @fg-content-box;
        }
    }
}

.content-component__tag-cloud > h2:first-child {
    margin-bottom: 0;
    border: @border;
    border-bottom: 0;
    background-color: @bg-content-box;
    color: @fg-content-box;
    border-top: @border;
    padding: 20px 20px 10px 20px;
}

#tagcloudWrapper,
.tag-cloud__wrapper {
    margin: 0;
    padding: 2rem;
    border: @border;
    border-top: 0;
    border-bottom: 0;
    background-color: @bg-content-box;
    color: @fg-content-box;
    &:last-child {
        border-bottom: @border;
    }
    & + h2 {
        margin-bottom: 0;
        padding: 10px 20px;
        border: @border;
        border-top: 0;
        border-bottom: 0;
        background-color: @bg-content-box;
        color: @fg-content-box;
    }

    .tagcloudElement[class^='tag'] a {
        // color: @fg-content-box !important;
    }
}

#tags {
    h2 {
        padding: 2rem;
    }
}

.tpl-tags {
    /* ELEMENT */
    &__content {
        h1 {
        }
    }
    &__text,
    #tagcloudWrapper {
        //    	padding: 10px;
        //    	border: @border;
        //    	background-color: @bg-content-box;
        & + h2 {
            //    		margin-bottom: 0;
            //	    	padding: 10px;
            //	    	border: @border;
            //	    	border-top: 0;
            //	    	border-bottom: 0;
            //	    	background-color: @bg-content-box;
        }
    }
    &__text {
        margin-bottom: 0;
        border-bottom: 0;
    }
    #tagcloudWrapper {
        margin: 0;
        border-top: 0;
        border-bottom: 0;
        &:last-child {
            border-bottom: @border;
        }
    }
}

each(range(100),{
    .tag@{value} a { color: @lk-content-box !important; }
});

.tag-cloud {
    h2 {
        margin-bottom: 0;
        padding: 10px;
        border: @border;
        border-top: 0;
        border-bottom: 0;
        background-color: @bg-content-box;
    }
}

