/* ==============================================================
   Search Standard
   style declarations for searchOptions.xhtml
   ==============================================================*/
/* SEARCH STANDARD */
/* BLOCK */
.search-standard {
    //	background-color: @bg-content-box;
    //  	border: @border;
    /* ELEMENT */
    &__help {
        button {
            color: @fg-general;
        }
    }
    &__title {
        h2 {
            color: @fg-content-box;
        }
    }
    &__input {
    }
    &__input-slim {
    }
    &__input-label,
    &__filter-label {
    }
    &__input-label {
    }
    &__input-autocomplete {
        .ui-autocomplete {
            .ui-autocomplete-input {
                background: @white;
                & when (@colorModes = true) {
                    .mx-dark-mode-rules ({
						background: @white; 
						color: @black;
					});;
                    .mx-light-mode-rules ({
						background: @white;
					});;
                }
            }
        }
    }
    &__filter {
    }
    &__filter-label {
    }
    &__filter-radio {
        label {
            font-size: @fontMedium;
            font-weight: 400;
            color: @fg-content-box;
        }
    }
    &__filter-select {
        .custom-control--select {
            & select {
                background: #f7f7f7;
                & when (@colorModes = true) {
                    .mx-dark-mode-rules ({
						background: @fg-general;
					});;
                    .mx-light-mode-rules ({
						background: #f7f7f7;
					});;
                }
            }
        }
    }
    &__filter-link {
        a {
        }
    }
    &__action {
        .btn--default {
            background: #f7f7f7;
            & when (@colorModes = true) {
                .mx-dark-mode-rules ({
					background: @fg-general;
				});;
                .mx-light-mode-rules ({
					background: #f7f7f7;
				});;
            }
        }
    }
    /* MODIFICATION */
}

/* AUTOCOMPLETE PANEL */
.ui-autocomplete-panel {
    ul.ui-autocomplete-items {
        li.ui-autocomplete-item {
            span.ui-autocomplete-query {
            }
        }
        li.ui-state-highlight {
        }
    }
}

